.select-style {
  border-radius: 10px;
  border-color: rgba(255, 255, 255, 0);
  background-color: #ffffff00; }

.mealy-machine table tr td.crossed {
  position: relative; }
  .mealy-machine table tr td.crossed:before, .mealy-machine table tr td.crossed:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    background-color: #f88045;
    width: 2px;
    height: 30px; }
  .mealy-machine table tr td.crossed:before {
    -webkit-transform: translate(-50%, -50%) rotate(-45deg);
    -ms-transform: translate(-50%, -50%) rotate(-45deg);
    transform: translate(-50%, -50%) rotate(-45deg); }
  .mealy-machine table tr td.crossed:after {
    -webkit-transform: translate(-50%, -50%) rotate(45deg);
    -ms-transform: translate(-50%, -50%) rotate(45deg);
    transform: translate(-50%, -50%) rotate(45deg); }

