.header {
  width: 100%;
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  transition: 200ms; }
  .header p {
    margin: 0;
    padding: 0; }
  .header .time {
    letter-spacing: 0.05em;
    font-size: 1rem; }
  .header .date {
    letter-spacing: 0.1em;
    font-size: 1rem; }



